import { Component, OnInit, ViewEncapsulation } from "@angular/core";
import { FormsModule } from "@angular/forms";
import { Hero } from "../interfaces/hero";

import { HeroService } from "../services/hero.service";
import { MessageService } from "../services/message.service";

@Component({
  selector: "app-heroes",
  templateUrl: "./heroes.component.html",
  styleUrls: ["./heroes.component.css"],
  encapsulation: ViewEncapsulation.None
})
export class HeroesComponent implements OnInit {
  myHeroesList: Hero[];
  selectedHero: Hero;

  hero: Hero = {
    id: 1,
    name: "windstorm"
  };

  constructor(
    private heroService: HeroService,
    private messages: MessageService
  ) {}

  ngOnInit() {
    this.initObjects();
    this.getHeroesList();
  }

  initObjects(): void {
    this.selectedHero = { id: 1, name: "dummy" };
  }

  getHeroesList(): void {
    // this.myHeroesList=this.heroService.getHeroes();
    this.heroService
      .getHeroes()
      .subscribe(myHeroData => (this.myHeroesList = myHeroData));
  }

  onSelectHero(myHero: Hero): Hero {
    this.messages.add("Hero has been clicked: " + myHero.name);
    return (this.selectedHero = myHero);
  }

  // add(name: string): void {
  //   name = name.trim();
  //   if (!name) {
  //     return;
  //   }
  //   this.heroService.addHero({ name } as Hero).subscribe(hero => {
  //     this.myHeroesList.push(hero);
  //   });
  // }

  // delete(hero: Hero): void {
  //   this.myHeroesList = this.myHeroesList.filter(h => h !== hero);
  //   this.heroService.deleteHero(hero).subscribe();
  // }
}



